
"use client";

import { useState } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { generateStudyPlan } from "@/ai/flows/personalized-study-plan";
import { Button } from "@/components/ui/button";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { CardContent, CardFooter } from "@/components/ui/card";
import { Loader2, Wand2 } from "lucide-react";
import { useToast } from "@/hooks/use-toast";
import { useUser, useFirestore, useCollection, useMemoFirebase } from "@/firebase";
import { collection, doc, addDoc } from 'firebase/firestore';


const formSchema = z.object({
  subject: z.string().min(2, "Subject is required."),
  curriculum: z.string().min(2, "Curriculum is required."),
  educationalMaterials: z.string().min(2, "Please list available materials."),
});

type FormData = z.infer<typeof formSchema>;

export function StudyPlanForm() {
  const { user } = useUser();
  const firestore = useFirestore();
  const [studyPlan, setStudyPlan] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const { toast } = useToast();

  const studentProfilesQuery = useMemoFirebase(() => {
    if (!user || !firestore) return null;
    return collection(firestore, `users/${user.uid}/studentProfiles`);
  }, [user, firestore]);

  const { data: studentProfiles, isLoading: isLoadingProfiles } = useCollection(studentProfilesQuery);
  const studentProfile = studentProfiles?.[0];

  const form = useForm<FormData>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      subject: "",
      curriculum: "",
      educationalMaterials: "",
    },
  });

  async function onSubmit(data: FormData) {
    if (!studentProfile) {
        toast({
            variant: "destructive",
            title: "Profile Not Found",
            description: "Please complete your student profile first.",
        });
        return;
    }

    setIsLoading(true);
    setStudyPlan(null);
    try {
      const result = await generateStudyPlan({
        name: studentProfile.name,
        age: studentProfile.age,
        gradeLevel: studentProfile.gradeLevel,
        country: studentProfile.country,
        preferredLearningLanguage: studentProfile.preferredLearningLanguage,
        ...data,
      });
      setStudyPlan(result.studyPlan);

      // Save the study plan to Firestore
      if (user && firestore) {
        const studyPlansCollection = collection(firestore, `users/${user.uid}/studentProfiles/${studentProfile.id}/studyPlans`);
        await addDoc(studyPlansCollection, {
            subject: data.subject,
            content: result.studyPlan,
            studentProfileId: studentProfile.id,
            createdAt: new Date(),
        });
      }

    } catch (error) {
      console.error("Error generating study plan:", error);
      toast({
        variant: "destructive",
        title: "Error",
        description: "Failed to generate study plan. Please try again.",
      });
    } finally {
      setIsLoading(false);
    }
  }

  if (isLoadingProfiles) {
    return (
        <div className="p-6 text-center">
            <Loader2 className="mx-auto h-8 w-8 animate-spin text-primary" />
            <p className="mt-2 text-muted-foreground">Loading profile...</p>
        </div>
    )
  }

  return (
    <>
      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)}>
          <CardContent className="space-y-4">
            <FormField
              control={form.control}
              name="subject"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Subject</FormLabel>
                  <FormControl>
                    <Input placeholder="e.g., Mathematics" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="curriculum"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Curriculum / Syllabus</FormLabel>
                  <FormControl>
                    <Input placeholder="e.g., Common Core, GCSE" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="educationalMaterials"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Available Educational Materials</FormLabel>
                  <FormControl>
                    <Textarea
                      placeholder="e.g., 'Algebra 1' textbook, Khan Academy account"
                      {...field}
                    />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
          </CardContent>
          <CardFooter>
            <Button type="submit" disabled={isLoading || !studentProfile}>
              {isLoading ? (
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              ) : (
                <Wand2 className="mr-2 h-4 w-4" />
              )}
              Generate Plan
            </Button>
          </CardFooter>
        </form>
      </Form>
      {isLoading && (
         <div className="p-6 text-center">
            <Loader2 className="mx-auto h-8 w-8 animate-spin text-primary" />
            <p className="mt-2 text-muted-foreground">Generating your personalized plan...</p>
         </div>
      )}
      {studyPlan && (
        <div className="p-6 border-t">
          <h3 className="text-xl font-bold mb-4 font-headline">Your Study Plan</h3>
          <div className="prose prose-sm max-w-none rounded-md border bg-secondary/50 p-4 whitespace-pre-wrap">
            {studyPlan}
          </div>
        </div>
      )}
    </>
  );
}
